	import {
			mapState,
	} from 'vuex';
	export default {
		data() {
			return {
				TabCur: 0,
				scrollLeft: 0,
				navList: ['今日','昨日'],
				amount: '',
				commission: ''
			};
		},
		onReady () {
			this.sendRequest();
		},
		computed: {
			...mapState(['userInfo'])
		},
		methods: {
			tabSelect(e) {
				this.TabCur = e.currentTarget.dataset.id;
				this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
			},
			async sendRequest(){
				try {
					const amount = await this.$request.post('/balance/index',{
						header:{
							token: this.userInfo.token
						}
					})
					const commission = await this.$request.post('/balance/income-this-month-and-last-month',{
						header:{
							token: this.userInfo.token
						}
					})
					this.amount = amount.data.data;
					this.commission = commission.data.data;

					this.$common.checkStatus(amount.data.code)
				} catch (error) {
					console.error('error:', error);
				}
			},
		},
		//点击导航栏 buttons 时触发
    onNavigationBarButtonTap(e) {
        const index = e.index;
        if (index === 0) {
            uni.navigateTo({
								url: './statement'
						});
        }
    }
	}
